input[type="checkbox"],
input[type="radio"] {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 0;
}
table.table .label-table {
  margin: 0;
  padding: 0;
  line-height: 15px;
  height: 15px;
}
[type="checkbox"] + label,
[type="radio"] + label {
  position: relative;
  cursor: pointer;
  display: inline-block;
  font-size: 1rem;
}
[type="checkbox"],
[type="radio"] {
  position: absolute;
  left: -9999px;
  visibility: hidden;
}
.btn-rounded {
  border-radius: 10em;
}
button,
input {
  overflow: visible;
}
button,
input,
optgroup,
select,
textarea {
  margin: 0;
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
}
input[type="radio" i],
input[type="checkbox" i] {
  background-color: initial;
  cursor: default;
  border: initial;
}
[type="checkbox"] + label:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 17px;
  height: 17px;
  z-index: 0;
  border: 1.5px solid #8a8a8a;
  border-radius: 1px;
  margin-top: 2px;
  -webkit-transition: 0.2s;
  transition: 0.2s;
}
.light-blue-skin input[type="checkbox"]:checked + label:before {
  border-right: 2px solid #b4c2c9;
  border-bottom: 2px solid #b4c2c9;
}
[type="checkbox"]:checked + label:before {
  top: -4px;
  left: -3px;
  width: 12px;
  height: 22px;
  border-style: solid;
  border-width: 2px;
  border-color: transparent #4285f4 #4285f4 transparent;
  -webkit-transform: rotate(40deg);
          transform: rotate(40deg);
  -webkit-transform-origin: 100% 100%;
          transform-origin: 100% 100%;
}
